.accordion {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  margin: 60px 0 80px;
  gap: 160px;
}

.accordion__item {
  transition: 0.3s all ease 0s;
}

.accordion__header {
  display: flex;
  justify-content: space-between;
  align-items: start;
  cursor: pointer;
  transition: color 0.2s ease-out;
  font-size: 16px;
  font-weight: 500;
  line-height: 20.4px;
  text-align: left;
}

.accordion__header::after {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  background-size: 24px;
  content: "";
  transition: transform 0.2s ease-out;
  background-image: url("../img/svg/accordion-arrow.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
}

.accordion__item_show .accordion__header::after,
.accordion__item_slidedown .accordion__header::after {
  transform: rotate(180deg);
}

.accordion__header:hover {
  color: #dfe0e2;
}

.accordion__body {
  font-size: 12px;
  font-weight: 400;
  line-height: 15.3px;
  text-align: left;
  margin-top: 32px;
}

.accordion__body-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.accordion__body-item {
  display: flex;
  gap: 8px;
  font-size: 16px;
  font-weight: 400;
  line-height: 20.4px;
  text-align: left;
}
.accordion__body-item a {
  color: #ffffff;
}

.accordion__body-wrapper {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.accordion__item:not(.accordion__item_show) .accordion__body {
  display: none;
}

.collapse:not(.show) {
  display: block;
}

@media (max-width: 1439px) {
  .accordion {
    gap: 112px;
  }
}
@media (max-width: 1279px) {
  .accordion {
    gap: 54px;
    margin-bottom: 70px;
  }
  .accordion__header {
    font-size: 14px;
    line-height: 17.85px;
  }
  .accordion__body-item {
    font-size: 14px;
    line-height: 17.85px;
  }
  .accordion__body-list {
    gap: 12px;
  }
}
@media (max-width: 1279px) and (max-width: 1023px) {
  .accordion {
    gap: 38px;
  }
}
@media (max-width: 1279px) and (max-width: 767px) {
  .accordion {
    margin: 60px 0;
    grid-template-columns: 1fr;
    gap: 20px;
  }
  .accordion__item {
    padding-bottom: 20px;
    border-bottom: 1px solid #ffffff;
    font-size: 12px;
    line-height: 15.3px;
  }
}
@media (max-width: 1279px) and (max-width: 374px) {
  .accordion {
    margin-bottom: 40px;
  }
}
